* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 50px; /* Ensure enough space for the footer */
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px; /* Fixed padding syntax */
  box-shadow: 0 0 3px gray;
  margin-bottom: 20px; /* Add margin to separate from content */
}

/* transaction page */

/* antd icons */
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: grey;
}
